@import 'main.scss';

#header_main{
    width: 100%;
    height: 100vh;
    //background-color: $bg;
    background:linear-gradient(40deg,$blue,$pink);
    display: flex;
    align-items: center;
    //animation: bg-anim 7s ease-in-out infinite;
    //background-size: 200% 200%;
    
}

.header_block1{
    width: 45%;
    height: auto;
    padding-left: 70px;
    padding-right: 10px;
}


.header_button{
    //background-color: $pink;
    margin-top: 20px;
    padding: 15px 30px 15px 30px;
    border: none;
    border: $pink solid 2px;
    background-color: rgba($color: #000000, $alpha: 0.0);
    font-family: $font7;
    font-size: 15px;
    font-weight: 700;
    color: white;
    cursor: pointer;
    transition: 0.5s;
}

.header_button:hover{
    background-color: $pink;
    color: white;
    transition: 0.5s;
}

.header-t1{
    font-family: $font7;
    font-size: 30px;
    color: white;
}
.header-t2{
    font-family: $font7;
    font-size: 40px;
    color: white;
    font-weight: 900;
}

.header-t3{
    font-family: $font7;
    font-size: 20px;
    color: white;
}

//responsive 900px

@media only screen and (max-width: 900px){
    .header_block1{
        width: 100%;
        height: auto;
        padding-left: 30px;
        padding-right: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .header-t1{
      width: 100%;
      text-align: center;
    }
    .header-t2{
        width: 100%;
        text-align: center;
      }
      .header-t3{
        width: 100%;
        text-align: center;
      }
}

//responsive 600

@media only screen and (max-width: 600px){
    .header-t1{
        font-size: 20px;
    }
    .header-t2{
        font-size: 30px;
    }
    .header-t3{
        font-size: 15px;
    }
}