@import 'main.scss';

#about_main{
    width: 100%;
    min-height: 100vh;
    padding: 90px 70px 0px 70px;
}

.bloc_title{
    font-family: $font7;
    font-size: 28px;
    font-weight: 600;
    color: $blue;
}

.about_box{
    width: 100%;
    display: flex;
    align-items: center;
    margin-top: 40px;
}

.about_box1{
    width: 56%;
    overflow: hidden;
}
.about_image{
    width: 100%;
    height: auto;
}


.about_box2{
    width: 44%;
    padding-left: 20px;
    text-align: justify;
    font-family: $font7;
    color: $black1;
    font-size: 17px;
}

//responsive

@media only screen and (max-width: 900px){
    #about_main{
        padding: 90px 30px 0px 30px;
    }

    .bloc_title{
      width: 100%;
      text-align: center;
    }

    .about_box{
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    .about_box1{
        width: 100%;
        overflow: hidden;
        padding-bottom: 10px;
    }

    .about_box2{
        width: 100%;
        overflow: hidden;
        padding-left: 0px;
        padding-bottom: 25px;
    }
}