@import 'main.scss';

.navMain{
    width: 100%;
    height: 60px;
    background-color:rgba($color: #ffffff, $alpha: 0.0);
    padding: 0px 70px 0px 70px;
    position: fixed;
    color: white;
}

.bars_icon{
    float: right;
    height: 60px;
    line-height: 60px;
    font-size: 25px;
    cursor: pointer;
    display: none;
}

.nav{
    width: 100%;
    height: 60px;
    background-color:rgba($color: #ffffff, $alpha: 1.0);
    box-shadow: 0px 0px 8px rgba($color: #656969, $alpha: 0.5);
    padding: 0px 70px 0px 70px;
    position: fixed;
    color: $blue;
}


.logo_container{
    height: 60px;
    width: auto;
    overflow: hidden;
    padding: 5px 0px 5px 0px;
    float: left;
}

.logo_img{
    height: 40px;
    width: auto;
}

.link_container{
    height: 60px;
    float: right;
    display: flex;
    align-items: center;

}

.ul_main{
    list-style-type: none;
}


.il_main{
    display: inline-block;
    height: 30px;
    line-height: 30px;
    margin-left: 10px;
    font-family: $font7;
    cursor: pointer;
    font-weight: 600;
    
}

ul li:hover{
    border-bottom: $pink 2px solid;
}


//responsive 900px
@media only screen and (max-width: 900px){
    .bars_icon{
        display:block;
    }
    .li_main:hover{
        border-bottom: none;
    }

    .link_container{
        background-color: $pink;
        width: 50%;
        height: auto;
        position: absolute;
        top: 60px;
        right: 0px;
        visibility: visible;
    }

    .ul_main{
       width: 100%;
       height: auto;
       border: solid red 1px;
    }

    .il_main{
        display: block; 
        width: 100%;
        text-align: center; 
        border-bottom: solid 2px $blue;
        height: 50px;
        line-height: 50px;
    }

    .navMain{
        padding: 0px 30px 0px 30px;
    }

    .avtive{
        visibility: visible;
        opacity: 1;
    }
    .on{
        visibility: visible;
        opacity: 1;
        transition:0.7s
    }
    .off{
        visibility: hidden;
        opacity: 0;
        transition:0.4s
    }
    

}
