@import 'main.scss';

#service_main{
    width: 100%;
    min-height: 100vh;
    padding: 90px 70px 30px 70px;
    background-color: $bg;
}

.services_box{
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
    grid-gap: 40px;
    margin-top: 40px;
}

.box{
    text-align: center;
    padding-bottom: 10px;
}

.box_icon_container{
    width: 100%;
    height: auto;
    padding-top: 10px;
}

.service_icon{
    width: 30%;
    height: auto;
}

.service_title{
    font-family: $font7;
    font-weight: 20px;
    color: $blue;
    font-weight: 700;
    padding-top: 5px;
}

.service_text{
    padding: 20px 20px 0px 20px;
    font-family: $font7;
    color: $black1;
    text-align: center;
    text-align: center;
    -webkit-line-clamp:6;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.service_next_box{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.service_next{
    width: 50px;
    height: 50px;
    background-color: $blue;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin-top: 10px;
    font-size: 20px;
    cursor: pointer;
}

.service_next:hover{
    background-color: $pink;
}

//responsive

@media only screen and (max-width: 900px){
    #service_main{
        padding: 90px 30px 30px 30px;

    }
}