@import 'main.scss';

.footer_main{
    width: 100%;
    height: auto;
}

.footer1{
    width: 100%;
    min-width: 200px;
    background-color: rgb(73, 72, 72);
    padding: 20px 70px 20px 70px;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
    grid-gap: 40px;
}

.f1_title{
    color: white;
    font-family: $font7;
    font-size: 20px;
    font-weight: 600;
}

.f1_info{
    width: 100%;
    color: rgb(231, 229, 229);
    margin-top: 10px;
    ul{
        list-style-type: none;
    }
}

input{
    width: 100%;
    padding: 7px;
    margin-bottom: 5px;
    outline: none;
}
textarea{
    width: 100%;
    height: 100px;
    outline: none;
}

button{
    padding: 10px 40px 10px 40px;
    background-color: $pink;
    color: white;
    font-family: $font7;
    border: none;
    cursor: pointer;
}

button:hover{
    background-color: $blue;
}

//footer2

.footer2{
    width: 100%;
    background-color: rgb(46, 46, 46);
    padding: 20px 70px 20px 70px;
    color: white;
    text-align: center;


}
.icont{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.f2_icon{
    margin: 0px 3px 0px 3px;
    font-size: 30px;
}
span{
    font-family: $font7;
    font-size: 13px;
}

//responsive

@media only screen and (max-width: 900px){
    .footer1{
        padding: 20px 30px 20px 30px;
        text-align: center;
    }

    .footer2{
        padding: 20px 30px 20px 30px;
    }
    button{
        width: 100%;
    }
}